.create-quiz-wrapper {
    display: flex;
    align-items: flex-start;
    width: 100%;
 }

 .create-quiz-wrapper .main {
    width: 100%;
    padding-top: 20px;
    font-family: 'Ubuntu', sans-serif;
 }
 
 .create-quiz-wrapper .main .header {
    width: 90%;
    margin: 0 auto;
    margin-bottom: 25px;
    font-size: 1.5em;
    text-align: center;

 }
 
 .create-quiz-wrapper .form {
    width: 90%;
    margin: 0 auto;
    padding: 20px;
 }
 .create-quiz-wrapper .select {
    margin-top: 20px;
 }
 .create-quiz-wrapper .checkbox {
    margin-top: 30px;
    color: rgb(110, 108, 108);
    display: flex;
    align-items: center;
    width: 300px;
    justify-content: space-between;
 }
 
 .create-quiz-wrapper .input {
    width: 200px !important;
    margin: 10px 0;
 }
 .create-quiz-wrapper .add-question, .add-answer {
    box-shadow: 2px 2px 4px 5px rgba(195, 45, 45, 0.2);
    border-radius: 15px;
    padding: 10px 0;
    text-align: center;
    font-size: 1em;
    margin: 30px 0;
    cursor: pointer;
 }
 
 .create-quiz-wrapper .btn-wrapper {
    display: flex;
    align-items:center;
 }
 
 .create-quiz-wrapper .btn-wrapper .btn {
    color: white;
    border-radius: 10px !important;
 }
 .create-quiz-wrapper .btn-wrapper .btn:first-child {
    background-color: red;
 }
 .create-quiz-wrapper .question {
    display: flex;
    position: relative;
    background-color: #eff0f1;
    padding: 12px;
    width: 90%;
    box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.2);
    margin: 10px 0;
    border-left: 5px solid #00aaf1;
 }
 
 .create-quiz-wrapper .question div {
    margin-right: 40px;
 }
 .create-quiz-wrapper .question .delete {
    position: absolute;
    right: 20px;
    background-color: red;
    border-radius: 10px !important;
    color: white;
    padding: 8px;
    top: 5px;
 }
 
 .create-quiz-wrapper .save-quiz {
    border-radius: 10px;
    color: white;
 }
 
