.view-quiz {
    width: 98%;
    margin: 0 auto;
    margin-top: 1%;
    padding-top: 50px;
    height: calc(98% - 60px);
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
}

.view-quiz .not-auth {
    width: 100%;
    text-align: center;
    margin-top: 27%;
    font-size: 2.7em;
}

.view-quiz .content {
    width: 100%;
    
}

.view-quiz .header {
    width: 100%;
    background-color: white;
    color: black;
    text-align: center;
    font-size: 2.2em;
    letter-spacing: 2px;
    font-weight: 350;
    border-bottom: 4px solid #dadfea;
    padding: 15px 0;
}

.view-quiz .body {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.view-quiz .body .left {
    color: black;
    background-color: white;
    flex: .5 1;
    position: relative;
}

.view-quiz .body .left .description {
    margin: 30px 0 26px 20px;
} 

.view-quiz .body .left .comments {
    height: 600px;
    overflow-y: scroll;
    background-color: #dfaee5;
    padding-bottom: 25px;
    width: 430px;
}

.view-quiz .body .left .comments::-webkit-scrollbar {
    display: none;
}

.view-quiz .body .left .comments .comment {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    border-radius: 30px;
    padding: 6px;
    margin: 45px 0; 
    width: -webkit-fit-content; 
    width: -moz-fit-content; 
    width: fit-content;
    position: relative;
    margin-left: 60px;
    padding-left: 15px;
    padding-right: 15px;
}

.view-quiz .body .left .comments .sent-from-creator {
    margin-left: auto;
    margin-right: 60px;
    flex-direction: row-reverse;
    padding-right: 10px;
}

.view-quiz .body .left .comments .sent-from-creator div:last-child {
    right: -100px;
}

.view-quiz .body .left .comments .comment div:last-child {
    position: absolute;
    bottom: -18px;
    width: 150px;
}

.view-quiz .body .left .comments .comment img {
    width: 40px;
    height: 40px;
    position: absolute;
    left: -50px;
}

.view-quiz .body .left .comments .sent-from-creator .sent-from-img {
    width: 40px;
    height: 40px;
    position: absolute;
    left: 190px
}

.view-quiz .body .left .comments .input-field {
    width: 430px;
    position: absolute;
    bottom: 0;
    background-color: #f8bde4;
}

.view-quiz .body .left .comments .input-field input {
    width: 400px;
    outline: none;
    border: none;
    font-size: 1em;
    border-radius: 25px;
    padding: 15px;
}

.view-quiz .body .left .comments .input-field button {
    outline: none;
    border: none;
    font-size: 1em;
    border-radius: 25px;
    padding: 15px;
    width: 430px;
    color: white;
    background-color: #d1ac30;
    cursor: pointer;
}

.view-quiz .body .right {
    flex: .5 1;
    background-color: #f1e9e9;
    color :black;
}

.view-quiz .body .right .questions-num {
    background-color :white;
    width: 100%;
    font-size: 1.8em;
    text-align: center;
    padding: 20px 0;
}

.view-quiz .body .right .questions-wrapper {
    padding: 10px;
    overflow-y: scroll;
    height: 600px;
    position: relative;
}

.view-quiz .body .right .no-scroll {
    overflow: hidden !important;
}

.view-quiz .body .right .questions-wrapper .question {
    margin: 15px 0;
    padding: 13px;
    background-color :white;
    border-left: 20px solid #00aaf1;
    display: flex;
    align-items: center;
    justify-content: space-around;
    box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.4);
}

.view-quiz .body .right .hidden {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
    text-align: center;
}

.view-quiz .body .right .hidden div {
    margin-top: 40%;
    font-size: 1.5em;
    font-weight: 600;
}

.view-quiz .footer {
    width: 100%;
    background-color: rgb(235, 225, 225);
    color: black;
    padding: 40px 0;
    display: grid;
    align-content: center;
    justify-content: center;
}
.view-quiz .footer .buttons-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.view-quiz .footer .buttons-wrapper button {
    margin: 0 40px;
    color: white;
    outline: none;
    border: none;
    font-size: 1em;
    padding: 13px 23px;
    cursor: pointer;
    background-color: rgba(94,92,236, 1) !important;
}

.view-quiz .footer .buttons-wrapper button:first-child {
    margin: 0 40px;
    color: white;
    background-color: rgba(94,92,236, 1) !important;
}
 .take-quiz-wrapper {
         width: 98%;
         margin: 0 auto;
         background-color: white;
         color: black;
         margin-top: 1%;
         height: calc(98% - 5px);
         position: relative;
     }

     .take-quiz-wrapper .content .header {
         position: relative;
         padding: 20px;
         font-size: 2em;
         box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.3);
         display: flex;
         align-items: center;
         justify-content: space-between;
         padding-left: 50px;
         letter-spacing: 1px;
         padding-bottom: 5px;
     }

     .take-quiz-wrapper .content .header .center {
         position: absolute;
         left: 0;
     }

     .take-quiz-wrapper .content .body {
         display: flex;
         background-color: #e8e9eb;
     }

     .take-quiz-wrapper .content .body .left {
         flex: 0.5 1;
         position: relative;
         min-height: 450px;
         height: 600px;
     }

     .take-quiz-wrapper .content .body .left .question-name {
         width: 88%;
         margin: 0 auto;
         border-radius: 10px;
         font-size: 2em;
         margin-top: 40px;
         padding: 25px 15px;
     }

     .take-quiz-wrapper .content .body .left .question-bubble-wrapper {
         position: absolute;
         bottom: 30px;
         left: 0;
         right: 0;
         overflow-x: scroll;
         padding: 30px;
     }

     .take-quiz-wrapper .content .body .left .question-bubble-wrapper .question-bubble {
         border-radius: 100%;
         box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.4);
         text-align: center;
         padding: 20px 25px;
         margin: 0 20px;
         cursor: pointer;
         background-color: white;
     }

     .take-quiz-wrapper .content .body .right {
         flex: .5 1;
     }

     .take-quiz-wrapper .content .body .right .answers-wrapper {
         width: 95%;
         margin: 0 auto;
         margin-top: 80px;
     }

     .take-quiz-wrapper .content .body .right .answers-wrapper .answer {
         font-size: 1em;
         padding: 15px 20px;
         margin: 30px 0;
         color: white;
         background-color: rgb(19, 17, 17);
         box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.3);
         cursor: pointer;
         transition: 0.2s all;
     }

     .take-quiz-wrapper .content .body .right .answers-wrapper .answer:hover {
         background-color: rgba(112, 152, 114, 0.6);
         color: white;
     }

     .take-quiz-wrapper .content .body .right .answers-wrapper .selected {
         background-color: #0b3c18;
         font-size: 1.4em;
         padding: 20px 20px;
         color: white;
         margin: 30px 0;
         border-radius: 30px;
         cursor: pointer;
         transition: 0.2s all;
     }

     .take-quiz-wrapper .footer {
         width: 100%;
         box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.3);
         height: 100%;
         padding: 40px 0;
     }

     .take-quiz-wrapper .content .buttons-wrapper {
         width: 100%;
         z-index: 1;
         margin: 0 auto;
         display: flex;
         align-items: center;
         justify-content: space-around;
     }

     .take-quiz-wrapper .content .buttons-wrapper button {
         color: white;
         background-color: rgb(168, 31, 31);
         font-size: 1em;
         outline: none;
         border: 1px solid black;
          padding: 15px;
         cursor: pointer;
        
     }

     .bubble-correct {
         background-color: #0e6123 !important;
         color: white;
     }

     .selected-bubble {
         background-color: rgb(249, 212, 110) !important;
         color: white;
     }

     .modal-wrapper-quiz {
         display: none;
         position: absolute;
         -webkit-backdrop-filter: blur(4px);
                 backdrop-filter: blur(4px);
         background-color: rgba(0, 0, 0, 0.3);
         position: absolute; 
         top: 0;
         left: 0;
         z-index: 3;
         width: 100vw;
         height: 100vh;
         display: grid;
         align-content: center;
         justify-content: center;
     }

     .modal-wrapper-quiz .content {
         text-align: center;
         font-size: 1.3em;
         background-color: white;
         padding: 20px;
         border-radius: 20px;
         width: 40vw;
     }

     .modal-wrapper-quiz .buttons-wrapper {
         display: flex;
         align-items: center;
         justify-content: space-evenly;
         margin-top: 40px;
     }
     .modal-wrapper-quiz .buttons-wrapper button {
